import { _decorator, Component, Label, Node, ScrollView } from "cc";
import { basePageView } from "../common/basePageView";
import GlobalData from "../home/GloabalClass";
import { ticketsUsdt } from "./drawModel";
const { ccclass, property } = _decorator;

const openRewardPos = 1000
const drawView_arrow = 'drawView_arrow'

export interface QueryDrawRespDTO {
  confirm: boolean;
  example: false;
  //开奖信息是否确认：false:未确认,true:已确认

  count: number;
  //当前参加抽奖人数

  drawId: number;
  //当前奖池ID

  drawTime: number;
  //当前抽奖剩余开奖时间，时间戳

  join: boolean;
  openDrawId: number;
  //已开奖的奖池ID

  usdt: number;
  //中奖奖品

  win: boolean;
}

@ccclass("drawView")
export class drawView extends basePageView {
  @property(Node)
  walletConnectBtns: Node;

  @property(Node)
  unBindWalletInfo: Node;

  @property(Node)
  bindWalletInfo: Node;

  @property(Label)
  address: Label;

  @property(Node)
  home: Node;

  @property(Node)
  joinBtn: Node;

  @property(Node)
  arrow: Node;

  @property(Node)
  arrow_s: Node;

  //门票和usdt数量设置
  @property(Label)
  tickets: Label;

  @property(Label)
  usdt: Label;

  //开奖信息
  @property(Label)
  join_count: Label;

  @property(Label)
  timer: Label;

  timer_add = 0
  drawLuckyInfo: QueryDrawRespDTO;

  async start() {
    if(localStorage.getItem(drawView_arrow)){
      this.arrow.active = false
      this.arrow_s.active = true
    }
  }

  setJoinBtnUseAble(use: boolean){
    if(use){

    }else{
      
    }
  }

  update(deltaTime: number) {
    this.timer_add = this.timer_add + deltaTime
    if(this.timer_add >= 1){
      this.drawLuckyInfo.drawTime = this.drawLuckyInfo.drawTime - 1000
      if(this.drawLuckyInfo.drawTime <= 5000){
        this.joinBtn.active = false

      }
      const current_time = this.formatTimestamp(this.drawLuckyInfo.drawTime)
      this.timer.string = current_time
      this.timer_add = 0
    }
  }

  controlUnbindBtnVisible(visible: boolean) {
    this.unBindWalletInfo.active = visible;
  }

  controlBindBtnVisible(visible: boolean) {
    this.bindWalletInfo.active = visible;
  }

  controlConnectWalletBtnsVisible(visible: boolean) {
    this.walletConnectBtns.active = visible;
  }

  showConnectWalletBtns() {
    this.controlConnectWalletBtnsVisible(true);
    this.home.getChildByName("tap_btns").active = false;
  }

  setWalletInfo(address: string) {
    GlobalData.wallet_address = address;
    this.controlBindBtnVisible(false);
    this.controlUnbindBtnVisible(true);
    this.address.string = `${address.substring(0, 6)}...${address.substring(
      address.length - 6
    )}`;
  }

  //设置当前的倒计时
  formatTimestamp(timestamp: number): string {
    const date = new Date(timestamp);
    const now = new Date();
    let years = now.getFullYear() - date.getFullYear();
    let months = now.getMonth() - date.getMonth();
    let weeks = Math.floor((now.getTime() - date.getTime()) / (7 * 24 * 60 * 60 * 1000));
    let days = now.getDate() - date.getDate();
    let hours = now.getHours() - date.getHours();
    let minutes = now.getMinutes() - date.getMinutes();
    let seconds = now.getSeconds() - date.getSeconds();
  
    // 处理跨日的情况
    if (seconds < 0) {
      seconds += 60;
      minutes--;
    }
    if (minutes < 0) {
      minutes += 60;
      hours--;
    }
    if (hours < 0) {
      hours += 24;
      days--;
    }
    if (days < 0) {
      days += 7;
      weeks--;
    }
  
    // 调整年份和月份，因为年份和月份的计算可能不准确
    if (months < 0 || (months === 0 && (days < 0 || (days === 0 && hours < 0)))) {
      years--;
      months += 12;
    }
  
    return `${years} 年 ${months + 1} 月 ${weeks} 周 ${days} 天 ${hours} 时 ${minutes} 分 ${seconds} 秒`;
  }

  //设置当前获奖的信息
  setDrawLuckRewardInfo() {}

  //设置当前开奖情况的信息
  setDrawLuckInfo(rewardInfo :QueryDrawRespDTO) {
    this.drawLuckyInfo = rewardInfo
    this.join_count.string = `${rewardInfo.count}`

  }

  //展示当前的开奖信息
  showOpenRewardInfo(){

  }

  //设置当前的门票和usdt的信息
  setCurrentTicketsUsdtCount(info: ticketsUsdt){
    this.tickets.string = `${info.tiket}`
    this.usdt.string = `${info.usdt}`
  }

  closeBtnConnectWallets() {
    this.controlConnectWalletBtnsVisible(false);
    this.home.getChildByName("tap_btns").active = true;
  }

  markArrow(scroll: ScrollView, data){
    localStorage.setItem(drawView_arrow, 'drawView_arrow')
    this.arrow.active = false
    this.arrow_s.active = true

    if(scroll.getScrollOffset().y >= openRewardPos){

    }
    console.log('=====scroll', scroll, data)
  }
}
